html {
    font-size: 10px;
    font-family: 'sans-serif'
}
p{
    font-size: 1.6rem;

}
h1 {
    font-family:  'billabong';
    text-align: center;
    font-size: 13rem;
    font-weight:100;
    margin: 2rem 0;
    letter-spacing: -1px;
    color : black;
}

h1 a {
    text-decoration: none
}

@font-face {
    font-family: 'billabong';
    src: url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.woff");
}

.photo-grid {
    max-width: 1000px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.figure {
  border: 1px solid #d3d3d3; 
  padding: 2rem;
  -webkit-flex-basis: calc(33.333% - 4rem);
          flex-basis: calc(33.333% - 4rem); 
  -webkit-flex-grow: 1; 
          flex-grow: 1;
  margin: 0 2rem 2rem 2rem;
}

.photo {
    width: calc(100% + 4rem);
    margin: -2rem 0rem 0rem -2rem;
}

.button-container{
 display: -webkit-flex;
 display: flex;
 -webkit-justify-content: center;
         justify-content: center;
}

button, .button {
    border: 2px solid #f2f2f2;
    background: none;
    -webkit-flex-basis: 48%;
            flex-basis: 48%;
    display: inline-block;
    line-height: 2;
    text-decoration: none;
    padding: 5px;
    text-align: center;
    font-size: 15px;
    color: red;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    box-sizing: padding-box;
}

.add-icon{
   margin: 20px auto;
   display: table;
   padding: 40px;
   background: url("https://image.flaticon.com/icons/svg/60/60740.svg") center no-repeat; 
   background-size: contain;
   border: none; 
}

.form {
    margin: 0 auto;
    background: white;
    max-width: 360px;
    padding: 45px;
    text-align: center;
}

.form input {
    font-family: sans-serif;
    background: #f2f2f2;
    width: 100%;
    border: none;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
    margin: 0 0 15px;
}

.form button{
    font-family: sans-serif;
    text-transform: uppercase;
    background: #065784;
    width: 100%;
    border: none;
    padding: 15px;
    color: white;
    font-size: 14px;
    cursor: pointer;

}

.form button:hover {
    background: #788589;
}

.figure .single-photo {
    -webkit-flex-basis: calc(33.333% - 4rem);
            flex-basis: calc(33.333% - 4rem);
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    margin: 0 2rem 2rem 2rem;
    padding: 2rem;
    border: 1px solid #d3d3d3;
    background: #fff;
    position: relative;
}
 
 
.single-photo {
    max-width: 1000px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    background: #fff;
    border: black;
}
 
 
.single-photo .figure {
    box-shadow: none;
    margin: 0 2rem 0 0;
    border: 0;
    padding: 0;
    -webkit-flex: 1 0 60%;
            flex: 1 0 60%;
    max-width: 60%;
}
 
.single-photo .comments {
    -webkit-flex: 1 0 40%;
            flex: 1 0 40%;
    max-width: 40%;
}
 
.single-photo .photo {
    width: 100%;
    margin: 0;
}
 
.photo {
    width: calc(100% + 4rem);
    margin-left: -2rem;
    margin-top: -2rem;
    max-width: none;
}

.comment{
    width: 100%
}
.comment p {
    border-bottom: 1px solid #d3d3d3;
    padding: 0.5rem 0;
}

.comment-form input,
.comment-form textarea {
    width: 100%;
    border: 0;
    font-size: 1.3rem;
    padding: 1rem 0;
    border-bottom: 1px solid #d3d3d3;
    outline: none;
    resize: vertical;
}

 
button:hover,.button:hover, button:focus, .button:focus {
    border-color: #00f;
    outline: 0;
}
 
.speech-bubble {
    background: url("https://d30y9cdsu7xlg0.cloudfront.net/png/51904-200.png") center no-repeat;
    background-size: contain;
    padding: 10px;
    margin: 5px;
    display: inline-block;
    border: none;
    position: relative;
}
 
 
.comment-count {
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: black
}
 
